:root {
  --antique-white: #faebd7;
  --gunmetal: #272d2d;
  --moonstone: #50b2c0;
}

body {
  background-color: var(--antique-white);
  color: var(--gunmetal);
}

h1, h2, h3, h4, h5, h6 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
}

p {
  font-family: Special Elite, serif;
}

.main_app {
  grid-template-rows: min-content 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr min(50%, 85ch) 1fr;
  gap: 0;
  display: grid;
}

.Sketch {
  grid-area: 2 / 1 / 5 / 4;
}

.Right-Margin {
  grid-area: 5 / 3 / 6 / 4;
}

.Left-Margin {
  grid-area: 5 / 1 / 6 / 2;
}

.Content {
  grid-area: 5 / 2 / 6 / 3;
}

.Top-Bar {
  grid-area: 1 / 1 / 2 / 4;
}

.Header {
  grid-area: 1 / 2 / 2 / 3;
}

.Sketch {
  flex-flow: row;
  justify-content: center;
  align-items: flex-start;
  padding-top: 1em;
  padding-bottom: 1em;
  display: flex;
  overflow: hidden;
}

.Sketch > canvas {
  box-shadow: 0em .1em .8em .1em var(--gunmetal);
}

.Top-Bar {
  background-color: var(--moonstone);
  box-shadow: 0 .5em 1em #0003;
}

.Header {
  flex-flow: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.Header > h1 {
  font-family: Montserrat Subrayada, sans-serif;
}

.Header > nav > .dropdown {
  display: inline-block;
  position: relative;
}

.Header > nav > .dropdown > button {
  background-color: var(--gunmetal);
  color: var(--antique-white);
  cursor: pointer;
  border: none;
  padding: 16px;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
}

.Header > nav > .dropdown > div {
  background-color: var(--antique-white);
  z-index: 1;
  width: 20ch;
  display: none;
  position: absolute;
  right: 0;
  box-shadow: 0 .5em 1em #0003;
}

.Header > nav > .dropdown > div > a {
  color: var(--gunmetal);
  padding: 12px 16px;
  font-family: Special Elite , serif;
  text-decoration: none;
  display: block;
}

.Header > nav > .dropdown > div > a:hover {
  background-color: var(--moonstone);
}

.Header > nav > .dropdown:hover > div {
  display: block;
}

